.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;

  @include OnTablet {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 30px;
  }

  @include OnDesktop {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
  }

  &--desktop {
    display: block;

    @include OnTablet {
      display: grid;
    }

    @include OnDesktop {
      display: grid;
    }
  }

  &--desktop-flex {
    display: flex;
    justify-content: center;
    gap: 30px;

    @include OnTablet {
      display: grid;
    }

    @include OnDesktop {
      display: grid;
    }
  }


  @include OnTablet {
    &__item-tablet {
      @for $start from 1 through 6 {
        @for $end from $start through 6 {
          &--#{$start}-#{$end} {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }


    @include OnDesktop {
        &__item {
          @for $start from 1 through 12 {
            @for $end from $start through 12 {
              &--#{$start}-#{$end} {
                grid-column: #{$start} / #{$end + 1};
              }
            }
          }
        }
     }
}



